@import '../fonts.scss';
@import 'variables.scss';
@import 'mixin.scss';

html {
    box-sizing: border-box;
    scroll-behavior: smooth;
}

*,
*::before,
*::after {
box-sizing: inherit;
}

body {
    font-family: 'tt_firs_neue', 'roboto', sans-serif;
    font-weight: 400;
    font-size: 16px;
    background-color: $colorWhite;
}

body.lock {
    overflow: hidden;
}

img {
    max-width: 100%;
}

button {
    margin: 0;
    padding: 0;
    border: none;
    background-color: transparent;
    cursor: pointer;
    outline: none;
    border: none;
}

h1,h2,h3,h4,h5,h6, p {
    margin: 0;
    padding: 0;
}

ul {
    list-style: none;
    margin: 0;
    padding: 0;
}

a {
    text-decoration: none;
    outline: none;
    border: none;
}

*:focus-visible {
    outline: 0;
}

.container {
    @include container();
}

.btn-orange {
    @include btnOrange();
    &:hover {
        @include btnOrangeHover();
    }
    &:focus-visible {
        @include btnOrangeFocus();
    }
    &:active {
        @include btnOrangeActive();
    }
}

.btn-transporent {
    @include btnTransparent();
    &:hover {
        @include btnTransparentHover();
    }
    &:focus-visible {
        @include btnTransparentFocus();
    }
    &:active {
        @include btnOrangeActive();
    }
}

.second-title {
    font-weight: 700;
    font-size: 24px;
    line-height: $lineHeigth;
    color: $colorBlack;
}

.therd-title {
    font-weight: 400;
    font-size: 24px;
    line-height: 22px;
    color: $colorBlack;
}

[class*="ground-pane"] {
    filter: grayscale(100%);
}

.ymaps-2-1-79-map-copyrights-promo {
    display: none;
}

.ymaps-2-1-79-copyright, .ymaps-2-1-79-copyright_logo_no,
.ymaps-2-1-79-copyright__fog, .ymaps-2-1-79-copyright__wrap, .ymaps-2-1-79-copyright__layout,
.ymaps-2-1-79-copyright__content-cell, .ymaps-2-1-79-copyright__content, .ymaps-2-1-79-copyright__text,
.ymaps-2-1-79-copyright__agreement, .ymaps-2-1-79-copyright__link  {
    display: none;
}

@media (max-width: 1110px) {
    .container {
        @include container1024();
    }
}

@media (max-width: 1023px) {
    .container {
        @include container768();
    }
}

@media (max-width: 767px) {
    .container {
        @include container320();
    }

    .second-title {
        font-size: 18px;
    }

    .therd-title {
        font-size: 18px;
    }
}
