@import '../utilitls/variables.scss';
@import '../utilitls/mixin.scss';

.projects {
    padding-top: 67px;
    margin-bottom: 66px;
    &__title {
        margin-bottom: 13px;
    }
    &__container {
        display: grid;
        grid-template-columns: repeat(12, 1fr);
        grid-template-rows: repeat(2, 1fr);
        gap: $gap;
        &-box {
            display: flex;
            flex-direction: column;
            width: 100%;
            position: relative;

            &-photo {
                width: 100%;
                height: 184px;
                margin-bottom: 17px;
                &-img {
                    object-fit: cover;
                    width: 100%;
                    height: 100%;
                }
            }
            &-hashtag {
                @include hashtag();
                padding: 5px 14px;
                top: 6px;
                right: 7px;
            }

            &-text {
                font-family: 'roboto';
                font-size: 16px;
                line-height: 22px;
                color: $colorBlack;
                margin-bottom: 12px;
            }

            &-date {
                display: flex;
                align-items: center;

                &-svg {
                    margin-right: 4px;
                }
                &-svg svg use {
                  fill: $colorGray;
                }
                &-day {
                    font-family: 'roboto';
                    font-size: 12px;
                    line-height: 22px;
                    color: $colorGray;

                }
            }
            &:nth-child(1) {
                grid-column: 1/ 3;
            }

            &:nth-child(2) {
                grid-column: 3/ 5;
            }

            &:nth-child(3) {
                grid-column: 5/ 7;
            }
        }


        &-bigbox {
            width: 100%;
            background-repeat: no-repeat;
            background-size: cover;
            position: relative;

            &-hastag {
                @include hashtag();
                padding: 6px 14px;
                top: 6px;
                right: 7px;
            }

            &-article {
                padding-top: 23px;
                padding-left: 30px;
                &-date {
                    display: flex;
                    align-items: center;
                    &-svg {
                        margin-right: 5px;
                        use {
                            fill: $colorWhite;
                        }
                    }
                    &-day {
                        font-family: 'roboto';
                        font-size: 12px;
                        line-height: 22px;
                        color: $colorWhite;
                    }
                }

                &-title {
                    font-weight: 700;
                    font-size: 24px;
                    line-height: 24px;
                    color: $colorWhite;
                    max-width: 293px;
                    width: 100%;
                    margin-bottom: 6px;
                }

                &-descr {
                    max-width: 292px;
                    width: 100%;
                    font-family: 'roboto';
                    font-size: 16px;
                    line-height: 22px;
                    color: $colorWhite;
                }

                &-btn {
                    width: 121px;
                    height: 50px;
                    position: absolute;
                    bottom: 31px;
                }
            }

            &:nth-child(4) {
                grid-column: 7/ 13;
                background-image: url('../img/project-fuji-1920.png');
            }

            &:nth-child(5) {
                grid-column: 1/ 7;
                background-image: url('../img/project-oblivion-1920.png');
            }
        }

        &-midbox {
            position: relative;
            &-photo {
                margin-bottom: 13px;
                height: 184px;
                &-img {
                    object-fit: cover;
                    width: 100%;
                    height: 100%;
                }
            }

            &-hashtag {
                @include hashtag();
                padding: 6px 14px;
                top: 6px;
                right: 7px;
            }
            &-text {
                font-family: 'roboto';
                font-size: 16px;
                line-height: 22px;
                margin-bottom: 12px;
            }

            &-date {
                display: flex;
                align-items: center;

                &-svg {
                    margin-right: 4px;
                }

                &-svg svg use {
                  fill: $colorGray;
                }

                &-day {
                    font-family: 'roboto';
                    font-size: 12px;
                    line-height: 22px;
                    color: $colorGray;
                }
            }
            &:nth-child(6) {
                grid-column: 7/ 10;
            }

            &:nth-child(7) {
                grid-column: 10/ 13;
            }
        }
    }
}

@media (max-width: 1110px) {
    .projects {
        &__container {
            grid-template-columns: (repeat(24, 1fr));
            grid-template-rows: repeat(3, 1fr);
            grid-auto-flow: dense;
            &-box {
                &:nth-child(1) {
                    grid-column: 1/ 7;
                }
                &:nth-child(2) {
                    grid-column: 7/ 13;
                }
                &:nth-child(3) {
                    grid-column: 13/ 25;
                }
            }

            &-bigbox {
                &:nth-child(4) {
                    grid-column: 1/ 16;
                    background-image: url('../img/project-fuji-1024.png');
                }

                &:nth-child(5) {
                    grid-column: 10/ 25;
                    background-image: url('../img/project-oblivion-1024.png');
                }
            }

            &-midbox {
                &:nth-child(6) {
                    grid-column: 16/ 25;
                }
                &:nth-child(7) {
                    grid-column: 1/ 10;
                }
            }
        }

    }
}

@media (max-width: 1023px) {
    .projects {
        &__container {
            grid-template-columns: repeat(12, 1fr);
            grid-template-rows: repeat(4, 1fr);
            &-box {
                &-hashtag {
                    @include hashtag320();
                    padding: 4px 14px;
                }
                &:nth-child(1) {
                    grid-column: 1/ 5;
                }
                &:nth-child(2) {
                    grid-column: 5/ 9;
                }
                &:nth-child(3) {
                    grid-column: 9/ 13;
                }
            }

            &-bigbox {
                &-hastag {
                    @include hashtag320();
                    padding: 4px 14px;
                }
                &:nth-child(4) {
                    grid-column: 1/ 13;
                    background-image: url('../img/project-fuji-768.png');
                }

                &:nth-child(5) {
                    grid-column: 1/ 13;
                    background-image: url('../img/project-oblivion-768.png');
                    order: 1;
                }
            }

            &-midbox {
                &-hashtag {
                    @include hashtag320();
                    padding: 4px 14px;
                }
                &:nth-child(6) {
                    grid-column: 1/ 7;
                }
                &:nth-child(7) {
                    grid-column: 7/ 13;
                }
            }
        }
    }
}

@media (max-width: 767px) {
    .projects {
        padding-top: 50px;
        &__container {
            grid-template-columns: 1fr;
            grid-template-rows: 1fr;
            &-box {
                &:nth-child(1) {
                    grid-column: 1/ 2;
                }
                &:nth-child(2) {
                    grid-column: 1/ 2;
                }
                &:nth-child(3) {
                    grid-column: 1/ 2;
                }
            }

            &-bigbox {
                &-article {
                    padding-top: 196px;
                    padding-left: 29px;
                    min-height: 422px;

                    &-date {
                        margin-bottom: 5px;
                    }

                    &-title {
                        margin-bottom: 11px;
                    }

                    &-descr {
                        font-size: 14px;
                        line-height: 18px;
                    }

                    &-btn {
                        width: 110px;
                        height: 39px;
                        font-size: 14px;
                        line-height: 16.41px;
                    }
                }
                &:nth-child(4) {
                    grid-column: 1/ 2;
                    background-image: url('../img/project-fuji-320.png');
                }

                &:nth-child(5) {
                    grid-column: 1/ 2;
                    background-image: url('../img/project-oblivion-320.png');
                    order: 1;
                }
            }

            &-midbox {
                &:nth-child(6) {
                    grid-column: 1/ 2;
                }
                &:nth-child(7) {
                    grid-column: 1/ 2;
                }
            }
        }
    }
}
