@import '../utilitls/variables.scss';
@import '../utilitls/mixin.scss';

.hero {
    &__grid {
        padding-top: 40px;
        margin-bottom: 68px;
        width: 100%;
        display: grid;
        grid-template-columns: repeat(2, 1fr);        
        gap: $gap;

        &-first {
            background-image: url('../img/hero-1-1920.png');
            background-repeat: no-repeat;
            background-size: cover;
            position: relative;
            
            &-article {
                padding-left: 58px;
                padding-top: 405px;
                margin-right: 15px;
                &-hashtag {
                    @include hashtag();
                    right: 15px;
                    top: 15px;
                    padding: 7px 15px 7px 14px;
                }
                &-master {
                    display: block;
                    font-family: 'roboto';
                    font-size: 16px;
                    line-height: $lineHeigth;
                    color: $colorWhite;
                }

                &-title {
                    max-width: 427px;
                    width: 100%;
                    margin-bottom: 21px;
                    font-weight: 700;
                    font-size: 36px;
                    line-height: 104%;
                    color: $colorWhite;
                }

                &-btn {
                    margin-left: 3px;
                    padding: 15.5px 22px;
                }
            }
        }
        &-second {
            &-title {
                margin-bottom: 19px;
                line-height: 104%;
            }
            &-list {
                display: grid;
                grid-template-columns: repeat(2, 1fr);
                grid-template-rows: 300px 257px;
                gap: $gap;
            }
            &-item {
                &:first-child {
                    grid-column: 1 / 3;
                }
                &-img {
                    object-fit: cover;
                    width: 100%;
                    height: 100%;
                }
            }
        }
    }
}

@media (max-width: 1110px) {
    .hero {
        &__grid {
            padding-top: 34px;
            &-first {
                background-image: url(../img/hero-1-1024.png);
                &-article {
                    padding-left: 30px;
                    padding-top: 423px;

                    &-master {
                        margin-bottom: 8px;
                    }

                    &-title {
                        font-size: 34px;
                        line-height: 100%;
                        margin-bottom: 23px;
                    }
                    &-btn {
                        margin-left: 0;
                    }
                }
            }
            &-second {
                &-title {
                    margin-bottom: 25px;
                }
                &-list {
                    grid-template-rows: 332px 186px;
                }
            }
        }
    }
}

@media (max-width: 1023px) {
    .hero__grid {
        padding-top: 81px;
        &-first {
            background-image: url('../img/hero-1-768.png');
            &-article {
                padding-left: 27px;
                padding-top: 223px;

                &-master {
                    margin-bottom: 12px;
                }

                &-title {
                    max-width: 239px;
                    line-height: 30.6px;
                    margin-bottom: 25px;
                }
            }
        }
        &-second-title {
            font-size: 22px;
            margin-bottom: 15px;
        }

        &-second-list {
            grid-template-rows: 228px 133px;
        }
    }
}

@media (max-width: 767px) {
    .hero__grid {
        grid-template-columns: 1fr;
        gap: 34px;
        padding-top: 76px;
        margin-bottom: 50px;

        &-first {
            background-image: url('../img/hero-1-768.png');
            &-article {
                padding-top: 234px;
                margin-right: 46px;
                margin-bottom: 30px;

                &-hashtag {
                    top: 6px;
                    right: 5px;
                    padding: 4px 7px;
                    font-size: 12px;
                    line-height: 117%;
                }

                &-master {
                    font-size: 14px;
                    line-height: 117%;
                    margin-bottom: 8px;
                }

                &-title {
                    max-width: 500px;
                    font-size: 24px;
                    line-height: 110%;
                    margin-bottom: 14px;
                }

                &-btn {
                    margin-left: 2px;
                    padding: 9px 21px;
                    font-size: 14px;
                    line-height: 117%;
                }
            }
        }

        &-second-title {
            font-size: 18px;
            line-height: 18.72px;
            margin-bottom: 17px;
        }

        &-second-list {
            grid-template-rows: 186px 105px;
        }
    }
}