@import '../utilitls/variables.scss';
@import '../utilitls/mixin.scss';

.about {
    background-color: $colorBlack;
    padding-bottom: 69px;
    &__title {
        color: $colorWhite;
        padding-top: 30px;
        margin-bottom: 3px;
    }

    &__container {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: $gap;
        &-left-descr {
            font-family: 'roboto';
            line-height: 28px;
            color: $colorWhite;
        }
        &-right {
            &-photo {
                height: 255px;
                width: 100%;
                margin-bottom: 22px;
                &-img {
                    object-fit: cover;
                    width: 100%;
                    height: 100%;
                }
            }
            &-title {
                font-weight: 400;
                font-size: 24px;
                line-height: 22px;
                color: $colorWhite;
                margin-bottom: 18px;
            }

            &-form {
                display: grid;
                gap: $gap;
                grid-template-columns: 4fr 1fr;

                &-input {
                    height: 50px;
                    max-width: 474px;
                    width: 100%;
                    background-color: $colorGrayDark;
                    outline: none;
                    border: none;

                    &[type="text"] {
                        padding-left: 23px;
                        font-family: 'roboto';
                        line-height: 117%;
                        color: $colorGrayVaryLight;
                    }
                }
                &-btn {
                    width: 126px;
                    height: 50px;                    
                }
            }
        }
    }
}

@media (max-width: 1110px) {
    .about {
        padding-bottom: 80px;
        &__container-right-photo {
            height: 332px;
        }
        &__container-right-form {
            grid-template-columns: 1fr;
            gap: 20px;
            &-input {
                max-width: 342px;
            }
        }
    }
}

@media (max-width: 1023px) {
    .about {
        padding-bottom: 64px;
        &__container-right-photo {
            height: 569px;
        }
        &__container-right-form-input {
            max-width: 461px;
        }
    }
}

@media (max-width: 767px) {
    .about {
        padding-bottom: 60px;
        &__title {
            margin-bottom: 5px;
        }

        &__container {
            grid-template-columns: 1fr;
            gap: 23px;
            &-left-descr {
                font-size: 14px;
                line-height: 25px;
            }

            &-right-photo {
                height: 278px;
                margin-bottom: 25px;
            }

            &-right-title {
                font-size: 18px;
                line-height: 22px;
                margin-bottom: 15px;
            }

            &-right-form-input {
                height: 40px;
                &[type="text"] {
                    font-size: 14px;
                    line-height: 16.41px;
                }
            }

            &-right-form-btn {
                width: 115px;
                height: 40px;
                font-size: 14px;
                line-height: 16.41px;
            }
        }
    }
}