@import '../utilitls/variables.scss';
@import '../utilitls/mixin.scss';

.footer {
    background-color: $colorBlack;
    &__container {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: $gap;
        align-items: flex-start;
        padding: 24px 0 16px 0;
        &-logo {
            &-copyright {
                padding-top: 4px;
                font-size: 14px;
                line-height: 18.06px;
                color: $colorGrayVaryLight;
            }
        }
        &-desr {
            &-text {
                font-family: 'roboto';
                font-weight: 400;
                font-size: 16px;
                line-height: 22px;
                color: $colorGrayVaryLight;
            }
            &-span {
                font-family: 'roboto';
                font-weight: 400;
                font-size: 16px;
                line-height: 22px;
                color: $colorGrayVaryLight;
            }
        }

        &-social {
            justify-self: end;
            &-span {
                display: block;
                margin-bottom: 8px;
                font-size: 16px;
                line-height: 22px;
                font-family: 'roboto';
                color: $colorGrayVaryLight;
            }
            &-list {
                display: flex;
                justify-content: center;
                &-item:first-child {
                    margin-right: 20px;
                }
                &-item-link svg use {
                  fill: $colorGrayLight;
                  transition: fill .3s ease-in-out;
                }

                &-item-link:hover svg use {
                  fill: $colorOrange;
                }

                &-item-link:focus-visible svg use {
                  fill: $colorBrownLight;
                }

                &-item-link:active svg use {
                  fill: $colorOrangeDark;
                }
            }
        }
    }
}

@media (max-width: 1110px) {
    .footer {
        &__container {
            grid-template-columns: .92fr 1fr .5fr;
            &-logo {
                padding-top: 7px;
                &-link-svg {
                    width: 166px;
                    height: 27px;
                }
            }
        }
    }
}

@media (max-width: 1023px) {
    .footer {
        &__container {
            grid-template-columns: 1fr 1fr;
            &-desr {
                display: none;
            }
        }
    }
}

@media (max-width: 767px) {
    .footer {
        position: relative;
        &__container {
            display: block;
            padding: 31px 0 29px 0;
            &-logo {
                position: absolute;
                width: 100%;
                top: 100%;
                left: 0;
                background-color: $colorGrayDark;
                text-align: center;
                padding-top: 13px;

                &-link-svg {
                    width: 112px;
                    height: 18px;
                }

                &-copyright {
                    font-size: 10px;
                    line-height: 12.9px;
                    padding-top: 1px;
                }
            }

            &-social {
                &-span {
                    text-align: center;
                }
            }
        }
    }
}
