.header {
  background-color: #202020;
  padding: 24px 0 22px 0;
}
.header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__burger {
  display: none;
}
.header__links {
  display: flex;
  width: 57.51%;
  margin-right: 53px;
}
.header__links-close {
  display: none;
}
.header__links-menu-nav {
  width: 100%;
}
.header__links-menu-nav-list {
  width: 100%;
  display: flex;
  justify-content: space-between;
}
.header__links-menu-nav-list-item-link {
  position: relative;
  color: #FFFFFF;
  line-height: 129%;
  z-index: 1;
  transition: color 0.4s ease-in-out;
}
.header__links-menu-nav-list-item-link:focus-visible {
  color: #FFFFFF;
}
.header__links-menu-nav-list-item-link::before {
  content: "";
  position: absolute;
  top: 0;
  left: -5px;
  bottom: 0;
  right: -5px;
  background-color: #BC572B;
  z-index: -1;
  opacity: 0;
  transition: opacity 0.4s ease-in-out;
}
.header__links-menu-nav-list-item-link:focus-visible::before {
  opacity: 1;
}
.header__links-menu-nav-list-item-link:hover:not(:focus-visible) {
  color: #FF6E30;
}
.header__links-menu-nav-list-item-link:active:not(:focus-visible) {
  color: #CE4307;
}
.header__links-phone {
  display: none;
}
.header__search {
  position: relative;
}
.header__search-form {
  position: absolute;
  width: 342px;
  height: 30px;
  top: 0;
  right: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #202020;
  z-index: 1;
  transform: scaleX(0);
  transition: transform 0.4s ease-in-out;
}
.header__search-form-close {
  width: 25px;
  height: 25px;
}
.header__search-form input {
  width: 308px;
  background-color: transparent;
  outline: none;
  border: none;
  border-bottom: 1px solid #FFFFFF;
}
.header__search-form.active {
  transform: scaleX(1);
}
.header__search-btn {
  width: 25px;
  height: 25px;
}
.header__search-btn svg use {
  fill: #FFFFFF;
  transition: fill 0.3s ease-in-out;
}
.header__search-btn:hover svg use {
  fill: #FF6E30;
}
.header__search-btn:focus-visible svg use {
  fill: #BC572B;
}
.header__search-btn:active svg use {
  fill: #CE4307;
}

