@import 'variables.scss';

@mixin container {
    max-width: $containerMaxWidth;
    width: 100%;
    padding: 0 50px;
    margin: 0 auto;
}

@mixin container1024 {
    padding: 0 31px;
}

@mixin container768 {
    padding: 0 28px 0 29px;
}

@mixin container320 {
    padding: 0 21px 0 21px;
}

@mixin btn {
    font-family: 'roboto';
    font-size: 16px;
    line-height: 117.2%;
    color: $colorWhite;
}

@mixin btnOrange {
    background-color: $colorOrange;
    border: 3px solid $colorOrange;
    transition: background-color .3s ease-in-out, border-color .3s ease-in-out;
    @include btn()
}

@mixin btnOrangeHover {
    background-color: $colorBrownLight;
    border-color: $colorBrownLight;
}

@mixin btnOrangeFocus {
    background-color: $colorBrownLight;
}

@mixin btnOrangeActive {
    background-color: $colorOrangeDark;
    border-color: $colorOrangeDark;
}

@mixin btnTransparent {
    border: 3px solid $colorOrange;
    transition: background-color .3s ease-in-out, border-color .3s ease-in-out;
    @include btn();
}

@mixin btnTransparentHover {
    background-color: $colorOrange;
}

@mixin btnTransparentFocus {
    background-color: $colorBrownLight;
}

@mixin btnTransparentActive {
    background-color: $colorOrangeDark;
    border-color: $colorOrangeDark;
}

@mixin widthSvg {
    width: 25px;
    height: 25px;
}

@mixin widthLogo1024 {
    width: 166px;
    height: 26.8px;
}

@mixin widthLogo320 {
    width: 111.55px;
    height: 18px;
}

@mixin widthSearchSvg320 {
    width: 20px;
    height: 20px;
}

@mixin hashtag {
    position: absolute;
    font-family: 'roboto';
    font-size: 14px;
    line-height: $lineHeigth;
    background-color: $colorBlack;
    color: $colorWhite;
}

@mixin hashtag320 {
    font-size: 12px;
    line-height: 117%;
}