@import '../utilitls/variables.scss';
@import '../utilitls/mixin.scss';

.header {
    background-color: $colorBlack;
    padding: 24px 0 22px 0;

    &__container{
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    &__burger {
        display: none;
    }

    &__links {
        display: flex;
        width: 57.51%;
        margin-right: 53px;
        &-close {
            display: none;
        }
        &-menu-nav {
            width: 100%;

            &-list {
                width: 100%;
                display: flex;
                justify-content: space-between;

                &-item-link {
                    position: relative;
                    color: $colorWhite;
                    line-height: $lineHeigth;
                    z-index: 1;
                    transition: color .4s ease-in-out;

                    &:focus-visible {
                        color: $colorWhite;
                    }
                    &::before {
                        content: '';
                        position: absolute;
                        top: 0;
                        left: -5px;
                        bottom: 0;
                        right: -5px;
                        background-color: $colorBrownLight;
                        z-index: -1;
                        opacity: 0;
                        transition: opacity 0.4s ease-in-out;
                    }

                    &:focus-visible::before {
                        opacity: 1;
                    }

                    &:hover:not(:focus-visible) {
                        color: $colorOrange;
                    }

                    &:active:not(:focus-visible) {
                        color: $colorOrangeDark;
                    }
                }
            }
        }
        &-phone {
            display: none;
        }
    }
    &__search {
        position: relative;

        &-form {
            position: absolute;
            width: 342px;
            height: 30px;
            top: 0;
            right: 0;
            display: flex;
            align-items: center;
            justify-content: space-between;
            background-color: $colorBlack;
            z-index: 1;
            transform: scaleX(0);
            transition: transform .4s ease-in-out;

            &-close {
                @include widthSvg();
            }

            input {
                width: 308px;
                background-color: transparent;
                outline: none;
                border: none;
                border-bottom: 1px solid $colorWhite;
            }
        }
        &-form.active {
            transform: scaleX(1);
        }

        &-btn {
            @include widthSvg();
        }
        &-btn svg use {
          fill: $colorWhite;
          transition: fill .3s ease-in-out;
        }

        &-btn:hover svg use {
          fill: $colorOrange;
        }

        &-btn:focus-visible svg use {
          fill: $colorBrownLight;
        }
        &-btn:active svg use {
          fill: $colorOrangeDark;
        }
    }
}

@media (max-width: 1110px) {
    .header {
        padding: 26px 0;
        &__logo {
            @include widthLogo1024();

            &-svg {
                @include widthLogo1024();
            }
        }
        &__links {
            width: 58%;
            margin-right: 49px;
        }
    }
}

@media (max-width: 1023px) {
    .header {
        position: relative;
        &__links {
            position: absolute;
            top: 100%;
            left: 0;
            width: 100%;
            padding: 11px 27px 13px 27px;
            margin-right: 0;
            background-color: $colorGrayDark;
        }
    }
}

@media (max-width: 767px) {
    .header {
        padding: 23px 0;
        &__logo {
            @include widthLogo320();
            &-svg {
                @include widthLogo320();
            }
        }

        &__burger {
            position: absolute;
            top: 100%;
            left: 0;
            width: 100%;
            display: block;
            background-color: $colorGrayDark;
            padding: 14px 0 11px 21px;
            &-btn svg use {
              fill: $colorWhite;
              transition: fill .3s ease-in-out;
            }

            &-btn:hover svg use {
              fill: $colorOrange;
            }

            &-btn:focus-visible svg use {
              fill: $colorBrownLight;
            }

            &-btn:active svg use {
              fill: $colorOrangeDark;
            }
        }

        &__links {
            display: block;
            width: 100vw;
            height: 100vh;
            z-index: 2;
            top: 0;
            padding: 25px 20px;
            background-color: $colorBlackTransparent;
            overflow: auto;
            transform: translateY(-100%);
            transition: transform .3s ease-in-out;
            &-close {
                display: block;
                margin-bottom: 16px;
            }
            &-menu-nav {
                margin-bottom: 200px;
                &-list {
                    flex-direction: column;
                    &-item:not(:last-child) {
                        margin-bottom: 30px;
                    }
                }
            }
            &-phone {
                display: block;
                &-tel {
                    display: flex;
                    align-items: center;
                    &-span {
                        font-weight: 500;
                        font-size: 16px;
                        line-height: 129%;
                        color: $colorOrange;
                    }
                }
            }
        }
        &__links.active {
            transform: translate(0);
        }

        &__search {
            position: static;
            &-form {
                width: 100%;
                padding: 30px;
                .header__search-form-input {
                    width: 99%
                }
            }
            &-btn {
                @include widthSearchSvg320();
                &-svg {
                    @include widthSearchSvg320();
                }
            }
        }
    }
}
