@import '../utilitls/variables.scss';
@import '../utilitls/mixin.scss';

.contacts {
    margin: 0 auto;
    max-width: 1390px;
    margin-bottom: 69px;
    &__title {
        padding: 0 50px;
        margin-bottom: 15px;
    }

    &__container {
        padding: 0 50px;
        display: grid;
        grid-template-columns: repeat(6, 1fr);
        grid-template-rows: 1fr;
        gap: $gap;
        &-box {
            grid-column: 1/ 5;
            position: relative;
            min-height: 460px;
            margin-top: 7px;
            &-map {
                width: 100%;
                height: 460px;
            }
            &-contact {
                position: absolute;
                bottom: 0;
                left: 0;
                max-width: 410px;
                width: 100%;
                height: 460px;
                background-color: $colorBlackTransparent;
                transform: scaleX(1);
                transition: transform .3s ease-in-out;
                &-btn {
                    position: absolute;
                    top: 20px;
                    right: 20px;
                }
                &-article {
                    padding-top: 174px;
                    padding-left: 60px;
                    margin-right: 50px;
                    &-title {
                        color: $colorWhite;
                        margin-bottom: 4px;
                    }
                    &-address {
                        font-family: 'roboto';
                        font-weight: 400;
                        font-size: 16px;
                        line-height: 28px;
                        color: $colorGrayLight;
                        margin-bottom: 5px;
                    }
                    &-tel {
                        display: flex;
                        align-items: center;
                        &-svg {
                            margin-right: 5px;
                        }
                        &-span {
                            font-weight: 500;
                            font-size: 16px;
                            line-height: 129%;
                            color: $colorOrange;    
                        }
                    }
                }
            }
            &-contact.not-active {
                transform: scaleX(0);
            }
        }

        &-request {
            grid-column: 5/ 7;
            &-title {
                margin-bottom: 5px;
            }
            &-descr {
                font-family: 'roboto';
                font-size: 16px;
                line-height: 22px;
                font-weight: 400;
                color: $colorGray;
                margin-bottom: 15px;
            }

            &-form {
                &-input {
                    display: block;
                    width: 100%;
                    height: 50px;
                    background-color: $colorGrayVaryLight;
                    border: none;
                    outline: none;
                    margin-bottom: 20px;
                    
                    &[type="text"], &[type="email"] {
                        font-family: 'roboto';
                        font-size: 16px;
                        line-height: 18.75px;
                        color: $colorGray;
                        padding-left: 23px;
                    }
                }

                &-textarea {
                    display: block;
                    width: 100%;
                    height: 150px;
                    background-color: $colorGrayVaryLight;
                    border: none;
                    outline: none;
                    margin-bottom: 20px;
                    padding-left: 23px;
                    font-family: 'roboto';
                    font-size: 16px;
                    line-height: 18.75px;
                    color: $colorGray;
                }
                &-btn {
                    padding: 12px 20px;
                }
            }
        }
    }
}

@media (max-width: 1110px) {
    .contacts {
        &__title {
            padding: 0 31px;
        }
        &__container {
            padding: 0 31px;
            grid-template-columns: repeat(24, 1fr);
            &-box {
                grid-column: 1/ 16;

                &-contact {
                    max-width: 700px;
                    height: 157px;
                    &-article {
                        padding-top: 25px;
                        padding-left: 30px;
                        margin-right: 0;
                    }
                }
            }

            &-request {
                grid-column: 16/ 25;
            }
        }
    }
}

@media (max-width: 1023px) {
    .contacts {
        &__title {
            padding: 0 28px 0 29px;
        }
        &__container {
            padding: 0 28px 0 29px;
            display: block;
            &-box {
                max-width: 1024px;
                width: 100%;
                height: 467px;
                margin-bottom: 23px;
                &-contact {
                    max-width: 1024px;
                }
            }
            &-request {
                width: 65.2%;
            }
        }
    }
}

@media (max-width: 767px) {
    .contacts {
        margin-bottom: 49px;
        &__title {
            padding: 0 21px;
        }
        &__container {
            padding: 0;
            &-box {
                height: 472px;
                &-contact {
                    &-article {
                        padding-top: 26px;
                        padding-left: 19px;
                        &-title {
                            margin-bottom: 9px;
                        }
                        &-address {
                            font-size: 14px;
                            line-height: 18px;
                            width: 87.4%;
                            margin-bottom: 12px;
                        }
                        &-tel {
                            &-svg {
                                width: 15px;
                                height: 15px;                                
                            }
                            &-span {
                                font-size: 14px;
                                line-height: 18.06px;
                            }
                        }
                    }
                }
            }
            &-request {
                padding: 0 21px;
                width: 100%;
                &-descr {
                    font-size: 14px;
                    line-height: 18px;
                    margin-bottom: 17px;
                }

                &-form {
                    &-input {
                        height: 40px;
                    }

                    &-btn {
                        padding: 9px 20px;
                        font-size: 14px;
                        line-height: 16.41px;
                    }
                }
            }
        }
    }
}